import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';

import { Nav, NavItem, NavbarBrand, Collapse, Button } from 'reactstrap';

import Image from 'components/Image';
import { getImage } from 'constants/images';

import styles from './Navbar.module.scss';

const Logo = getImage('common.logo');

class Navbar extends Component {
  state = {
    isFull: false,
    isLogo: false,
    collapsed: false,
  };

  componentDidMount() {
    window.addEventListener('scroll', this.onMovement);
    this.onMovement();
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.onMovement);
  }

  toggleNavbar = () => {
    const { collapsed } = this.state;

    this.setState({ collapsed: !collapsed });
  }

  onMovement = (e) => {
    const { isFull, isLogo } = this.state;

    if (!isFull && window.scrollY > 0) {
      this.setState({
        isFull: true,
      });
    }

    if (isFull && window.scrollY <= 0) {
      this.setState({
        isFull: false,
      });
    }

    if (!isLogo && window.scrollY > 200) {
      this.setState({
        isLogo: true,
      });
    }

    if (isLogo && window.scrollY <= 200) {
      this.setState({
        isLogo: false,
      });
    }
  }

  render() {
    const { isFull, isLogo, collapsed } = this.state;
    const { fixed } = this.props;

    return (
      <div className={[styles.nav, isFull || fixed ? styles.full : ''].join(' ')}>
        <Nav
          className={[styles.navInner, "main-margin d-none d-lg-flex", isLogo || fixed ? styles.innerShift : ''].join(' ')}
        >
          <NavbarBrand
            className={[styles.logo, isLogo || fixed ? styles.logoVisible : ''].join(' ')}
          >
            <Link to="/">
              <Image {...Logo} />
            </Link>
          </NavbarBrand>
          <NavItem>
            <Link to={{ pathname: '/', state: { hash: 'home' } }}>Home</Link>
          </NavItem>
          <NavItem>
            <Link to={{ pathname: '/', hash: '#portfolio', state: { hash: 'portfolio' } }}>Portfolio</Link>
          </NavItem>
          <NavItem>
            <Link to={{ pathname: '/', hash: '#highlights', state: { hash: 'highlights' } }}>Highlights</Link>
          </NavItem>
          <NavItem>
            <Link to={{ pathname: '/', hash: '#process', state: { hash: 'process' } }}>Process</Link>
          </NavItem>
          <NavItem>
            <Link to={{ pathname: '/', hash: '#about', state: { hash: 'about' } }}>About</Link>
          </NavItem>
          <NavItem>
            <Link to={{ pathname: '/', hash: '#faq', state: { hash: 'faq' } }}>Faq</Link>
          </NavItem>
          <NavItem>
            <Link to={{ pathname: '/', hash: '#enquire', state: { hash: 'enquire' } }}>Contact</Link>
          </NavItem>
          <NavItem>
            <Link to="/blog">Blog</Link>
          </NavItem>
        </Nav>
        <div
          className={["main-margin d-block d-lg-none", styles.navResp, isFull ? styles.full : ''].join(' ')}
        >
          <div className="d-flex align-items-center">
            <NavbarBrand
              className={[styles.logo, styles.logoVisible].join(' ')}
              href="/"
            >
              <Image {...Logo} />
            </NavbarBrand>
            <div className="flex-1" />
            <Button
              color="link"
              onClick={this.toggleNavbar}
              className={styles.bars}
            >
              <i className="fas fa-bars" />
              {/* <Image {...getImage('fontawesome.bars')} /> */}
            </Button>
          </div>
          <Collapse isOpen={collapsed} navbar>
            <Nav
              className={styles.navInnerResp}
            >
              <NavItem>
                <Link to={{ pathname: '/', state: { hash: 'home' } }} onClick={() => this.setState({ collapsed: false })}>Home</Link>
              </NavItem>
              <NavItem>
                <Link to={{ pathname: '/', hash: '#portfolio', state: { hash: 'portfolio' } }} onClick={() => this.setState({ collapsed: false })}>Portfolio</Link>
              </NavItem>
              <NavItem>
                <Link to={{ pathname: '/', hash: '#highlights', state: { hash: 'highlights' } }} onClick={() => this.setState({ collapsed: false })}>Highlights</Link>
              </NavItem>
              <NavItem>
                <Link to={{ pathname: '/', hash: '#process', state: { hash: 'process' } }} onClick={() => this.setState({ collapsed: false })}>Process</Link>
              </NavItem>
              <NavItem>
                <Link to={{ pathname: '/', hash: '#about', state: { hash: 'about' } }} onClick={() => this.setState({ collapsed: false })}>About</Link>
              </NavItem>
              <NavItem>
                <Link to={{ pathname: '/', hash: '#faq', state: { hash: 'faq' } }} onClick={() => this.setState({ collapsed: false })}>Faq</Link>
              </NavItem>
              <NavItem>
                <Link to={{ pathname: '/', hash: '#enquire', state: { hash: 'enquire' } }} onClick={() => this.setState({ collapsed: false })}>Contact</Link>
              </NavItem>
              <NavItem>
                <Link to="/blog" onClick={() => this.setState({ collapsed: false })}>Blog</Link>
              </NavItem>
            </Nav>
          </Collapse>
        </div>
      </div>
    );
  }
}

Navbar.defaultProps = {
  fixed: false,
};

Navbar.propTypes = {
  fixed: PropTypes.bool,
};

export default Navbar;
